import React, {Component} from 'react';
import "./ContactUs.scss";
import {validateEmail, validatePhoneNumber} from "../../Utils";
import CountryCodesMap from "../../countryCodes"

class ContactUs extends Component {

    state = {
        form: {
            firstName: "",
            lastName: "",
            email: "",
            phoneNumber: "",
            questions: ""
        },
        formErrors: {email: ""},
        formValidated: false
    };


    componentDidMount() {

    }


    onFormChange = (event) => {
        if (!event.target || !event.target.name) {
            return;
        }
        let form = {...this.state.form, [event.target.name]: event.target.value};

        this.setState(state => {
            return {...state, form: form}
        }, this.validateForm);
    };

    validatePhoneNumber = () => {
        let error = "";
        if (!validatePhoneNumber(this.state.form.phoneNumber)) {
            error = "This is not a correctly formatted Phone number.  Must have country code.";
        }
        let formErrors = {...this.state.formErrors, phoneNumber: error};
        this.setState({formErrors: formErrors})
    };

    validateEmail = () => {
        let error = "";
        if (!validateEmail(this.state.form.email)) {
            error = "This is not a correctly formatted Email.";
        }
        let formErrors = {...this.state.formErrors, email: error};
        this.setState({formErrors: formErrors})

    };

    submitForm = (event) => {
        event.preventDefault();
        console.log("Submit", this.state.form);
    };

    validateForm = () => {
        let form = this.state.form;
        let success = true;
        let formErrors = this.state.formErrors;
        for (let key in form) {
            if (key === "questions") {
                continue;
            }

            if (form.hasOwnProperty(key) && !form[key]) {
                success = false;
                formErrors[key] = `This field is required.`;
            } else {
                delete formErrors[key]
            }

            if (key === 'email') {
                this.validateEmail();
            }

        }

        this.setState({formValidated: success, formErrors: formErrors});
    };


    render() {

        const {form, formValidated, formErrors} = this.state;

        return (
            <div id="ContactUs">

                <h1 className="text-align-center font-weight-light color-primary"
                    ling-translate="ContactUsSectionTitle">Contact Us</h1>

                <form onSubmit={this.submitForm} className="form">

                    <div className="inputLabel">
                        <label ling-translate="ContactUsFirstName" required={true}>First Name</label>
                        <div className="flex-direction-column">
                            <input ling-translate="ContactUsFirstName" value={form.firstName} onChange={this.onFormChange} name={"firstName"}
                                   placeholder="First Name"
                                   className="input" type="text"/>
                            <div>
                                <span ling-translate="ContactUsFormError" className="formError">{formErrors.firstName}</span>
                            </div>
                        </div>
                    </div>

                    <div className="inputLabel">
                        <label ling-translate="ContactUsLastName" required={true}>Last Name</label>
                        <div className="flex-direction-column">
                            <input ling-translate="ContactUsLastName" value={form.lastName} onChange={this.onFormChange} name={"lastName"}
                                   placeholder="Last Name"
                                   className="input" type="text"/>
                            <div>
                                <span ling-translate="ContactUsFormError" className="formError">{formErrors.lastName}</span>
                            </div>
                        </div>
                    </div>

                    <div className="inputLabel">
                        <label ling-translate="ContactUsEmail" required={true}>Email</label>
                        <div className="flex-direction-column">
                            <input ling-translate="ContactUsEmail" value={form.email} onChange={this.onFormChange} name={"email"}
                                   placeholder="Email"
                                   className="input" type="email"/>
                            <div>
                                <span ling-translate="ContactUsFormError" className="formError">{formErrors.email}</span>
                            </div>
                        </div>
                    </div>

                    <div className="inputLabel">
                        <label ling-translate="ContactUsPhoneNumber" required={true}>Phone Number</label>
                        <div className="flex-direction-column">
                            <input ling-translate="ContactUsPhoneNumber" value={form.phoneNumber} onChange={this.onFormChange} name={"phoneNumber"}
                                   placeholder="+1 555 5555"
                                   className="input" type="tel"/>
                            <div>
                                {!formErrors.phoneNumber &&
                                <span ling-translate="ContactUsPhoneNumberHint" className={"inputHint"}>{"Please include country code as well."}</span>}
                                {formErrors.phoneNumber &&
                                <span ling-translate="ContactUsFormError" className={"formError"}>{formErrors.phoneNumber}</span>}
                            </div>
                        </div>
                    </div>

                    <div className="inputLabel">
                        <label/>
                        <textarea ling-translate="ContactUsAdditionalInfo" value={form.questions} onChange={this.onFormChange} name={"questions"}
                                  placeholder="(Optional) Any questions or comments you would like us to know ahead of time!"
                                  className="input" style={{minHeight: "170px", lineHeight: "22px"}}/>
                    </div>


                    <button ling-translate="SubmitButton" disabled={!formValidated} id="submitButton" type="submit"
                            className="standard-button">Submit
                    </button>

                </form>

            </div>
        );
    }
}

export default ContactUs;
